"use client";
import React, { useState, useEffect, ReactNode } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  YadakchiAdminLayout,
  ListPageLayout,
  Label,
  Badge,
  FormModal,
  Input,
  Button,
  type ColumnDefinition,
} from "@my-ds/design-system";

import { getTicketCategories, createTicketCategory, updateTicketCategory, } from "../../services/api/ticketCategoryService";
import { TicketCategory } from "@/services/api/types";

export default function TicketCategoryPage() {
  const [categories, setCategories] = useState<TicketCategory[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  const [isFormModalOpen, setIsFormModalOpen] = useState(false);
  const [editingCategory, setEditingCategory] = useState<TicketCategory | null>(null);
  const [formData, setFormData] = useState({ name: '', description: '' });
  const [actionLoading, setActionLoading] = useState(false);

  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  const fetchCategories = async () => {
    try {
      setIsLoading(true);
      const response = await getTicketCategories();
      setCategories(response);
    } catch (err) {
      setError(err instanceof Error ? err.message : "ุฎุทุง");
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const handleOpenAddModal = () => {
    setEditingCategory(null);
    setFormData({ name: '', description: '' });
    setIsFormModalOpen(true);
  };

  const handleOpenEditModal = (category: TicketCategory) => {
    setEditingCategory(category);
    setFormData({ name: category.name, description: category.description });
    setIsFormModalOpen(true);
  };

  const handleFormSubmit = async () => {
    if (!formData.name.trim()) {
      alert("ูุงู ุฏุณุชูโุจูุฏ ููโุชูุงูุฏ ุฎุงู ุจุงุดุฏ.");
      return;
    }
    setActionLoading(true);
    try {
      if (editingCategory) {
        await updateTicketCategory(editingCategory.id, { Name: formData.name, Description: formData.description });
      } else {
        await createTicketCategory({ Name: formData.name, Description: formData.description });
      }
      setIsFormModalOpen(false);
      await fetchCategories();
    } catch (err) {
      alert("ุฎุทุง ุฏุฑ ุฐุฎุฑูโุณุงุฒ.");
    } finally {
      setActionLoading(false);
    }
  };

  const categoryColumns: ColumnDefinition<TicketCategory>[] = [
    { accessorKey: 'id', header: 'ุดูุงุณู' },
    { accessorKey: 'name', header: 'ูุงู ุฏุณุชูโุจูุฏ', cell: (c) => <Label text={c.name} size="md" /> },
    { accessorKey: 'description', header: 'ุชูุถุญุงุช', cell: (c) => <Label text={c.description} size="md" variant="secondary" /> },
    { accessorKey: 'isActive', header: 'ูุถุนุช', cell: (c) => <Badge label={c.isActive ? "ูุนุงู" : "ุบุฑูุนุงู"} variant={c.isActive ? "success" : "neutral"} /> },
  ];
  
  const listPageProps = {
    listPageHeaderProps: {
      title: "ูุฏุฑุช ุฏุณุชูโุจูุฏ ุชฺฉุชโูุง",
      totalItems: categories.length,
      actions: <Button onClick={handleOpenAddModal}>ุงูุฒูุฏู ุฏุณุชูโุจูุฏ</Button> 
    },
    tableProps: {
      columns: categoryColumns,
      data: categories,
      emptyStateMessage: "ูฺ ุฏุณุชูโุจูุฏ ุงูุช ูุดุฏ.",
      rowActions: (category: TicketCategory) => ({
        onEdit: () => handleOpenEditModal(category),
        // onDelete: () => handleDelete(category.id),
      }),
    },
    // ฺูู ูพุฌูุดู ูุฏุงุฑูุ ุงู prop ุฑุง ูพุงุณ ููโุฏูู
    // paginationProps: { ... },
    isLoading: isLoading,
    error: error,
  };

  return (
    <>
      <YadakchiAdminLayout
        currentPath={pathname}
        renderLink={renderLink}
      >
        <ListPageLayout {...listPageProps} />
      </YadakchiAdminLayout>

      <FormModal
        isOpen={isFormModalOpen}
        onClose={() => setIsFormModalOpen(false)}
        title={editingCategory ? "ูุฑุงุด ุฏุณุชูโุจูุฏ" : "ุงูุฒูุฏู ุฏุณุชูโุจูุฏ ุฌุฏุฏ"}
        onConfirm={handleFormSubmit}
        isLoading={actionLoading}
        confirmText={editingCategory ? "ุฐุฎุฑู" : "ุงุฌุงุฏ"}
      >
        <form onSubmit={(e) => { e.preventDefault(); handleFormSubmit(); }} style={{display: 'flex', flexDirection: 'column', gap: '1rem'}}>
          <Input
            id="category-name"
            label="ูุงู ุฏุณุชูโุจูุฏ"
            value={formData.name}
            onChange={(e) => setFormData(prev => ({...prev, name: e.target.value}))}
            required
            autoFocus
          />
          <Input
            id="category-description"
            label="ุชูุถุญุงุช"
            value={formData.description}
            onChange={(e) => setFormData(prev => ({...prev, description: e.target.value}))}
          />
        </form>
      </FormModal>
    </>
  );
}